<template>
  <div class="comment-report-wrap page">
    <div class="radio-list" v-for="(item1, index1) in radiosData" :key="index1">
        <div class="list-name">
          <span class="primaryColor pr mt">*</span>
          <span>{{item1.title}}</span>
        </div>
        <van-radio-group class="radio-group" v-model="checkedData[index1]" direction="horizontal">
          <van-radio v-for="(item2, index2) in item1.datas" :key="item2.value"
            :class="[
              index2%3 === 2 ? 'radio-item2' : 'radio-item1',
              index2 === 6 ? 'radio-full' : ''
            ]"
            icon-size="0.37rem"
            :name="item2.value"
            >
            {{ item2.text }}
          </van-radio>
        </van-radio-group>
    </div>

    <van-button class="sub-btn" type="primary" :disabled="isDisabled" round size="large" @click="subDatas">提交</van-button>
  </div>
</template>

<script>
import { Button, RadioGroup, Radio, Toast } from 'vant'
export default {
  name: 'CommentReport',
  components: {
    'van-radio-group': RadioGroup,
    'van-radio': Radio,
    'van-button': Button
  },
  computed: {
    isDisabled () {
      return this.checkedData.some(item1 => !item1)
    }
  },
  data () {
    return {
      checkedData: ['', '', ''],
      radiosData: [
        {
          title: '违反法律法规',
          datas: [{ text: '违法违规', value: '违法违规' }, { text: '色情', value: '色情' }, { text: '低俗', value: '低俗' }, { text: '赌博诈骗', value: '赌博诈骗' }]
        },
        {
          title: '侵犯个人权益',
          datas: [{ text: '人身攻击', value: '人身攻击' }, { text: '侵犯隐私', value: '侵犯隐私' }]
        },
        {
          title: '违反法律法规',
          datas: [{ text: '垃圾广告', value: '垃圾广告' }, { text: '引战', value: '引战' }, { text: '剧透', value: '剧透' },
            { text: '刷屏', value: '刷屏' }, { text: '视频无关', value: '视频无关' }, { text: '抢楼', value: '抢楼' },
            { text: '青少年不良信息', value: '青少年不良信息' }, { text: '其他', value: '其他' }]
        }
      ]
    }
  },
  mounted () {
  },
  methods: {
    subDatas () {
      const subData = {
        comment_id: 1,
        reason: this.checkedData.join(','),
        user_id: '10240211'
      }
      this.$axios.post('/comment/report', subData).then((res) => {
        Toast.success('提交成功')
      }).catch((e) => {
        console.log(e)
      })
    }
  }
}
</script>

